@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.2/css/all.min.css";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header {
  z-index: 99;
  background-image: linear-gradient(#d8e2dc, #d3d3d3, #f5f3f4);
  justify-content: center;
  align-items: center;
  padding: .5rem;
  display: flex;
  position: sticky;
  top: 0;
}

header img {
  height: 2rem;
  padding-left: 1rem;
}

nav {
  width: 100vw;
  align-items: center;
  display: flex;
}

.nav-bar {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.dropdown-li {
  padding-right: 4rem;
  display: none;
  position: relative;
}

.cart {
  padding-right: 1rem;
}

nav i {
  font-size: x-large;
}

nav a {
  color: red;
  text-decoration: none;
}

nav a:hover {
  color: #db7093;
}

.search {
  background-color: red;
  display: flex;
}

form input {
  border: none;
}

button {
  color: #fff;
  background-color: red;
  border: none;
  padding: 5px;
}

.dropdown-li:hover .dropdown {
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  background-color: #c3c3c3;
  margin-right: 4rem;
  display: block;
  position: absolute;
  right: 0;
}

.dropdown {
  color: #fff;
  display: none;
}

header ul {
  width: 100%;
  justify-content: space-between;
  margin-bottom: 0;
  padding: 0 4rem;
  list-style-type: none;
  display: flex;
}

header .nav-desk a {
  color: red;
  border-top: 2px solid red;
  padding: 1rem;
  font-size: x-large;
  text-decoration: none;
}

header li:hover a {
  color: #f5f3f4;
  background-color: red;
}

.hero {
  width: 100vw;
}

.container {
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 2.9vw 0;
  display: flex;
}

hr {
  width: 1rem;
  background-image: linear-gradient(red, orange, #ff0, green, #00f, indigo, violet);
}

.container .slide {
  width: 50rem;
  height: 50rem;
  border: 1vw solid red;
  border-radius: 3rem;
  margin: 1rem;
  overflow: hidden;
  box-shadow: 0 0 40px 10px #969696;
}

.container .image, img {
  width: 100%;
  height: 100%;
}

.container .first .image {
  padding: 1rem 2rem;
  animation-name: or;
  animation-duration: 200s;
  animation-iteration-count: infinite;
}

@keyframes or {
  0% {
    transform: translateY(0);
  }

  25% {
    transform: translateY(-800%);
  }

  50% {
    transform: translateY(-1600%);
  }

  75% {
    transform: translateY(-2400%);
  }

  100% {
    transform: translateY(-3100%);
  }
}

.container .second .image {
  padding: 1rem 2rem;
  animation-name: zo;
  animation-duration: 150s;
  animation-iteration-count: infinite;
  transform: translateY(-2300%);
}

@keyframes zo {
  0% {
    transform: translateY(-2300%);
  }

  50% {
    transform: translateY();
  }

  100% {
    transform: translateY(0);
  }
}

footer {
  color: #b2b2b2;
  text-align: center;
  background-image: linear-gradient(to top, #d8e2dc, #d3d3d3, #f5f3f4);
  position: sticky;
  bottom: 0;
  left: 0;
}

footer p {
  margin-bottom: 0;
}

footer ul {
  justify-content: center;
  gap: 1rem;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

footer ul a {
  color: red;
  font-size: xx-large;
}

footer a:hover {
  color: #db7093;
}

@media screen and (max-width: 1200px) {
  .dropdown-li {
    display: inline-block;
  }

  nav {
    width: 100vw;
    display: flex;
  }

  .nav-bar {
    justify-content: space-between;
    padding: .5rem 3rem;
    display: flex;
  }

  header ul {
    display: none;
  }
}

@media screen and (max-width: 800px) {
  .container {
    flex-flow: column wrap;
    justify-content: space-around;
    align-items: center;
    padding: 1vw 0;
    display: flex;
  }

  .container .slide {
    width: 80vw;
    height: 100vw;
  }

  hr {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  header img {
    padding-left: 1vw;
  }

  .dropdown-li {
    padding: 1vw;
    display: inline-block;
  }

  .cart {
    padding-left: 1vw;
  }

  nav {
    width: 100vw;
    display: flex;
  }

  .nav-bar {
    justify-content: flex-end;
    padding: 1vw;
    display: flex;
  }

  .search, header ul {
    display: none;
  }
}

@media screen and (max-width: 150px) {
  header {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1vw;
    display: flex;
  }
}

